import React, {
  useState,
  ReactNode,
  FC
} from 'react';

export interface ButtonProps {
  children: ReactNode;
  onClick?: () => void;
  size?: 'xs' | 'sm' | 'md' | 'lg' | 'xl';
  style?: React.CSSProperties;
}

const Button: FC<ButtonProps> = ({
  children,
  size = 'sm',
  style,
  onClick
}: ButtonProps) => {

  const sizeValue =
    size === 'xs'
      ? {width: 80, height: 30}
      : size === 'sm'
        ? {width: 90, height: 40}
        : size === 'md'
          ? {width: 100, height: 45}
          : size === 'lg'
            ? {width: 110, height: 50}
            : {width: 120, height: 55};

  const defaultStyles = {
    ...style,
    ...sizeValue,
    border: '1px solid #ccc',
    borderRadius: '15px',
    } as React.CSSProperties;

  return (
    <button
      onClick={onClick}
      style={
        style
          ? style
          : defaultStyles
      }
    >
      {children}
    </button>
  )
}

export default Button;
